*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --main-text-color: #fff;
  --secondary-text-color: #000;
  --helpers-text-color: #8c8c8c;
  --accent-color: #ff6b01;
  --warning-text-color: #ff001b;
  --ft-text-color: #545454;
  --main-bg-color: #fff;
  --secondary-bg-color: #f7f7f7;
  --helpers-bg-color: #000;
  --overlay-color: rgba(0, 0, 0, .5);
  --btn-shadow: 0px 8px 43px rgba(255, 107, 1, .6);
  --bg-dark-theme: #292c35;
  --light-title-color: #000;
  --modal-team-role-color: #8c8c8c;
  --modal-team-member-icon: #000;
  --transition-params: 250ms ease-out;
}

h1, h2, h3, h4, h5, h6, p, ul {
  margin: 0;
  padding: 0;
}

img {
  margin: 0;
  display: block;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

body {
  min-height: 100vh;
  grid-template: auto 1fr auto / 1fr;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  display: grid;
  overflow-x: hidden;
}

body.overflow-hidden {
  overflow-y: hidden;
}

body.modal-open .backdrop {
  opacity: 1;
  visibility: visible;
  pointer-events: initial;
  overflow: hidden;
}

