.form-structor {
  overflow: hidden;
}

.form-structor:after {
  content: "";
  opacity: .8;
  background-color: #726c6a;
  background-position: 0 100%;
  background-repeat: no-repeat;
  background-size: 500px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.form-structor .signup {
  width: 65%;
  z-index: 5;
  transition: all .3s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.form-structor .signup.slide-up {
  transition: all .3s;
  top: 5%;
  transform: translate(-50%);
}

.form-structor .signup.slide-up .form-holder, .form-structor .signup.slide-up .submit-btn {
  opacity: 0;
  visibility: hidden;
}

.form-structor .signup.slide-up .form-title {
  cursor: pointer;
  font-size: 1em;
}

.form-structor .signup.slide-up .form-title span {
  opacity: 1;
  visibility: visible;
  margin-right: 5px;
  transition: all .3s;
}

.form-structor .signup .form-title {
  color: #fff;
  text-align: center;
  font-size: 1.7em;
}

.form-structor .signup .form-title span {
  color: rgba(0, 0, 0, .4);
  opacity: 0;
  visibility: hidden;
  transition: all .3s;
}

.form-structor .signup .form-holder {
  opacity: 1;
  visibility: visible;
  background-color: #fff;
  border-radius: 15px;
  margin-top: 50px;
  transition: all .3s;
  overflow: hidden;
}

.form-structor .signup .form-holder .input {
  box-shadow: none;
  height: 30px;
  width: 100%;
  border: 0;
  border-bottom: 1px solid #eee;
  outline: none;
  padding: 8px 15px;
  font-size: 12px;
  line-height: 30px;
  display: block;
}

.form-structor .signup .form-holder .input:last-child {
  border-bottom: 0;
}

.form-structor .signup .form-holder .input::-webkit-input-placeholder {
  color: rgba(0, 0, 0, .4);
}

.form-structor .signup .submit-btn {
  color: rgba(255, 255, 255, .7);
  width: 100%;
  cursor: pointer;
  opacity: 1;
  visibility: visible;
  background-color: rgba(0, 0, 0, .4);
  border: 0;
  border-radius: 15px;
  margin: 15px auto;
  padding: 15px 45px;
  font-size: 13px;
  font-weight: bold;
  transition: all .3s;
  display: block;
}

.form-structor .signup .submit-btn:hover {
  background-color: #d47e0d;
  transition: all .3s;
}

.form-structor .login {
  z-index: 5;
  background-color: #d47e0d;
  transition: all .3s;
  position: absolute;
  top: 20%;
  bottom: 0;
  left: 0;
  right: 0;
}

.form-structor .login:before {
  content: "";
  width: 200%;
  height: 250px;
  z-index: 4;
  background-color: #d47e0d;
  border-radius: 50%;
  transition: all .3s;
  position: absolute;
  top: -20px;
  left: 50%;
  transform: translate(-50%);
}

.form-structor .login .center {
  width: 65%;
  z-index: 5;
  transition: all .3s;
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.form-structor .login .center .form-title {
  color: #000;
  text-align: center;
  font-size: 1.7em;
}

.form-structor .login .center .form-title span {
  color: rgba(0, 0, 0, .4);
  opacity: 0;
  visibility: hidden;
  transition: all .3s;
}

.form-structor .login .center .form-holder {
  opacity: 1;
  visibility: visible;
  background-color: #fff;
  border: 1px solid #eee;
  border-radius: 15px;
  margin-top: 50px;
  transition: all .3s;
  overflow: hidden;
}

.form-structor .login .center .form-holder .input {
  box-shadow: none;
  height: 30px;
  width: 100%;
  border: 0;
  border-bottom: 1px solid #eee;
  outline: none;
  padding: 8px 15px;
  font-size: 12px;
  line-height: 30px;
  display: block;
}

.form-structor .login .center .form-holder .input:last-child {
  border-bottom: 0;
}

.form-structor .login .center .form-holder .input::-webkit-input-placeholder {
  color: rgba(0, 0, 0, .4);
}

.form-structor .login .center .submit-btn {
  color: rgba(255, 255, 255, .7);
  width: 100%;
  cursor: pointer;
  opacity: 1;
  visibility: visible;
  background-color: #726c6a;
  border: 0;
  border-radius: 15px;
  margin: 15px auto;
  padding: 15px 45px;
  font-size: 13px;
  font-weight: bold;
  transition: all .3s;
  display: block;
}

.form-structor .login .center .submit-btn:hover {
  background-color: rgba(0, 0, 0, .8);
  transition: all .3s;
}

.form-structor .login.slide-up {
  transition: all .3s;
  top: 90%;
}

.form-structor .login.slide-up .center {
  transition: all .3s;
  top: 10%;
  transform: translate(-50%);
}

.form-structor .login.slide-up .form-holder, .form-structor .login.slide-up .submit-btn {
  opacity: 0;
  visibility: hidden;
  transition: all .3s;
}

.form-structor .login.slide-up .form-title {
  cursor: pointer;
  margin: 0;
  padding: 0;
  font-size: 1em;
  transition: all .3s;
}

.form-structor .login.slide-up .form-title span {
  opacity: 1;
  visibility: visible;
  margin-right: 5px;
  transition: all .3s;
}

