.backdrop {
  width: 100%;
  height: 100%;
  background-color: var(--overlay-color);
  transition: opacity var(--transition-params), visibility var(--transition-params);
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: all .8s;
  position: fixed;
  top: 0;
  left: 0;
  overflow-x: hidden;
  overflow-y: auto;
}

.open {
  opacity: 1;
  visibility: visible;
  pointer-events: visible;
}

